*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-text-color: #fff;
  --secondary-text-color: #8d9289;
  --black: #000;
  --accent-color: #86e543;
  --background-color: #01383a;
  --secondary-bcg-color: #2f303a;
}

.button {
  background-color: var(--accent-color);
  color: var(--black);
  text-align: center;
  cursor: pointer;
  box-shadow: none;
  border: none;
  border-radius: 16px;
  padding: 16px 20px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
}

.btn-tabs {
  width: 124px;
  height: 35px;
  color: var(--accent-color);
  border: 1px solid var(--accent-color);
  background-color: rgba(0, 0, 0, 0);
  padding: 8px;
  font-size: 16px;
  transition: background-color .25s linear, box-shadow .25s linear;
}

@media screen and (min-width: 768px) {
  .btn-tabs {
    width: 132px;
    height: 51px;
  }
}

@media screen and (min-width: 1280px) {
  .btn-tabs {
    width: 168px;
  }
}

.btn-tabs:hover, .btn-tabs:focus {
  background-color: var(--accent-color);
  color: var(--black);
  box-shadow: 0 4px 4px rgba(134, 229, 67, .2);
}

.button--shopnow {
  transition: box-shadow .25s linear;
}

.button--shopnow:hover, .button--shopnow:focus {
  box-shadow: 0 8px 20px rgba(134, 229, 67, .5), 0 4px 10px rgba(255, 255, 255, .1);
}

.button--startshopping {
  text-transform: uppercase;
  color: var(--background-color);
  font-size: 32px;
  line-height: 1.19;
  transition: box-shadow .25s linear;
}

.button--startshopping:hover, .button--startshopping:focus {
  box-shadow: 0 8px 20px rgba(134, 229, 67, .5), 0 4px 10px rgba(255, 255, 255, .1);
}

.button--viewall {
  color: var(--background-color);
  background-color: var(--primary-text-color);
  padding: 16px 48px;
  font-size: 28px;
  line-height: 1.18;
  transition: box-shadow .25s linear;
}

.button--viewall:hover, .button--viewall:focus {
  box-shadow: 0 4px 20px rgba(255, 255, 255, .5), 0 4px 10px rgba(134, 229, 67, .1);
}

.button--buyitnow {
  text-transform: uppercase;
  color: var(--background-color);
  padding: 16px 64px;
  font-size: 32px;
  line-height: 1.19;
  transition: box-shadow .25s linear;
}

.button--buyitnow:hover, .button--buyitnow:focus {
  box-shadow: 0 8px 20px rgba(134, 229, 67, .5), 0 4px 10px rgba(255, 255, 255, .1);
}

.button--addtocart {
  text-transform: uppercase;
  color: var(--accent-color);
  background-color: var(--background-color);
  border: 1px solid var(--accent-color);
  padding: 16px 52px;
  font-size: 32px;
  line-height: 1.19;
}

.button--send {
  text-transform: uppercase;
  color: var(--primary-text-color);
  background-color: var(--background-color);
  border: 1px solid var(--primary-text-color);
  padding: 16px 112px;
  font-size: 32px;
  line-height: 1.19;
}

.button--filter {
  width: 168px;
  color: var(--accent-color);
  background-color: var(--background-color);
  border: 1px solid var(--accent-color);
  font-size: 16px;
  line-height: 1.19;
  transition: box-shadow .25s linear, color .25s linear, background-color .25s linear;
}

.button--filter:active {
  color: var(--background-color);
  background-color: var(--accent-color);
  box-shadow: 0 4px 10px rgba(134, 229, 67, .1), 0 4px 20px rgba(134, 229, 67, .4);
}

.button--cool {
  text-transform: uppercase;
  color: var(--background-color);
  border: 1px solid var(--accent-color);
  padding: 21px 0;
  font-size: 32px;
  line-height: 1.19;
  transition: box-shadow .25s linear;
}

.button--cool:active {
  box-shadow: 0 4px 10px rgba(134, 229, 67, .1), 0 4px 20px rgba(134, 229, 67, .4);
}

.button--bad {
  text-transform: uppercase;
  color: var(--accent-color);
  background-color: var(--background-color);
  border: 1px solid var(--accent-color);
  padding: 21px 0;
  font-size: 32px;
  line-height: 1.19;
}

