    color: var(--primary-text-color);
    font-family: Roboto, sans-serif;
    font-size: 24px;
    font-weight: 700;
  }
}

.button--shopnow {
  display: none;
}

.menu-button {
  width: 54px;
  height: 36px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
}

.menu-button .icon-close {
  opacity: 0;
  transition: opacity .25s linear;
}

.menu-button.is-open .icon-close {
  opacity: 1;
}

.menu-button.is-open .icon-open {
  opacity: 0;
  transition: opacity .25s linear;
}

@media screen and (min-width: 768px) {
  .header-wrap {
    background-image: url("bg-tablet.ee4bd156.jpg");
  }

  .button--shopnow {
    display: block;
  }

  .menu {
    background-image: url("background-menu-tablet.95afef33.jpg");
    background-size: cover;
    padding: 150px 100px;
  }
}

@media screen and (min-width: 1200px) {
  .header-wrap {
    background-image: url("bg-desktop.f0641bb7.jpg");
  }

  .header-wrap .container {
    max-width: 1200px;
  }

  .nav-list {
    gap: 48px;
    display: flex;
  }

  .menu-button {
    display: none;
  }

  .nav-list-link:after {
    content: "";
    width: 100%;
    height: 1px;
    background-color: var(--accent-color);
    opacity: 0;
    transition: opacity .25s linear, transform .25s linear;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: scale(0);
  }

  .nav-list-link:hover:after, .nav-list-link:focus:after {
    opacity: 1;
    transform: scale(1);
  }

  .menu {
    display: none;
  }
}

.menu {
  width: 100%;
  height: 100vh;
  z-index: 1;
  pointer-events: none;
  opacity: 0;
  background-image: url("background-menu-mobile.0b1878e2.jpg");
  background-size: contain;
  padding: 100px 50px;
  transition: transform .25s linear, opacity .25s linear;
  position: fixed;
  left: 0;
  overflow-y: scroll;
  transform: translateY(-100%);
}

.menu .menu-list {
  text-transform: uppercase;
  text-align: center;
  color: var(--primary-text-color);
  font-family: Roboto;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.18;
}

.menu.is-open {
  opacity: 1;
  transform: translateY(0);
}

.menu .menu-item:not(:last-child) {
  margin-bottom: 40px;
}

